body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: #E6E6FA;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
	float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 60px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 10px;
  text-align: center;
  width: 60px;
}


.redsquare {
  background: #99FFAA;
  border: 1px solid #999;
	float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 60px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 10px;
  text-align: center;
  width: 60px;
}


.table {
  padding: 20px;

}

.square:focus {
  outline: none;
}


.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.rank {
  display: inline-block;
  background-color: #ADFF2F;
  border-radius: 17%;
  padding: 1px 12px;
  border: 2px solid #E6E6FA;
}

.difficulty {
  display: inline-block;
  background-color: #ADFF2F;
  border-radius: 17%;
  padding: 1px 12px;
  border: 2px solid #E6E6FA;
}

.loadpuzzles {
  display: inline-block;
  background-color: #ADFF2F;
  border-radius: 17%;
  padding: 1px 12px;
  border: 2px solid #E6E6FA;
}


.start {
  display: inline-block;
  background-color: #ADFF2F;
  border-radius: 17%;
  padding: 1px 12px;
  border: 2px solid #E6E6FA; 
}

.check {
  display: inline-block;
  background-color: #ADFF2F;
  border-radius: 17%;
  padding: 1px 12px;
  border: 2px solid #E6E6FA;
}




